@component DateTimePicker {
	text-align: center;
	color: var(--fontColor-default);

	& .o-DatePicker,
	& .o-TimePicker {
		display: inline-block;
	}

	& .o-DateTimePicker__date,
	& .o-DateTimePicker__time {
		.o-Input__wrapper {
			display: none;
		}
	}
}



