























nav {
	position: fixed;
	z-index: 10;
	top: 0;
	left: 0;
	width: 100%;
	height: 60px;
	padding: 0 70px;
	background: white;
	text-align: left;
}
	/*box-shadow: 0 2px 8px 1px rgba(0, 0, 0, 0.08), 0 1px 2px 0px rgba(0, 0, 0, 0.1);*/
	nav a {
		display: inline-block;
		height: 100%;
		color: #34B697;
		font-size: 16px;
		line-height: 60px;
		text-decoration: none;
		vertical-align: middle;
	}
	nav a:not(:first-of-type):not(:last-of-type) {
			margin-left: 30px;
		}
	nav a .logo {
			display: inline-block;
			margin-top: -8px;
			width: 70px;
			vertical-align: middle;
		}
img {
	display: block;
	width: 100%;
	display: inline-block;
	vertical-align: middle;
}



