
































.tab {
	z-index: 9;
	position: fixed;
	top: 0;
	left: 0;
	padding-top: 60px;
	width: 260px;
	height: 100%;
	text-align: left;
}
	.tab ul {
		list-style: none;
		padding-left: 70px;
	}
	.tab li {
		margin-bottom: 1.2rem;
	}
	.tab li a {
			text-decoration: none;
			color: inherit;
			-webkit-transition: all ease 0.24s;
			transition: all ease 0.24s;
		}
	.tab li a:hover {
				color: #34B697;
			}



