


































.home {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	color: #34B697;
	text-align: center;
	background: no-repeat url("../icon/bg.svg") center 100%/ 100% auto;
}

	.home:after {
		display: inline-block;
		vertical-align: middle;
		content: '';
		height: 100%;
		width: 0;
	}

	.home .content {
		display: inline-block;
		vertical-align: middle;
		width: 500px;
	}

	.home .content .name {
			width: 168px;
			margin-bottom: 30px;
		}
.links {
	margin-top: 30px;
}
.links .o-Btn {
		width: 160px;
		vertical-align: middle;
	}
.links .o-Btn:not(:last-of-type) {
			margin-right: 44px;
			color: white;
		}
.license {
	position: absolute;
	bottom: 1rem;
	width: 100%;
}



